// Importa los estilos
import 'scss/main.scss';

// Importa jQuery primero
import $ from 'jquery';
window.$ = $;
window.jQuery = $;

// Importa Bootstrap después de jQuery
import 'bootstrap';

// Importa AOS y su CSS
import AOS from 'aos';
import 'aos/dist/aos.css';

// Inicializar AOS cuando el DOM esté completamente cargado
document.addEventListener('DOMContentLoaded', function() {
    AOS.init({
        duration: 800,   // Duración de la animación en milisegundos
        once: true      // Ejecutar la animación solo una vez
    });
});

// Espera a que el documento esté completamente cargado
$(document).ready(function() {
    // Añadir sombra a la navbar al hacer scroll
    $(window).on('scroll', function () {
        const scroll = $(window).scrollTop();
        $(".navbar.navbar-expand-lg").toggleClass("shadow", scroll > 0);
    });
});

document.addEventListener('DOMContentLoaded', function() {
    // Definir la función getOffsetTop()
    function getOffsetTop() {
        const header = document.querySelector('.navbar');
        return header ? header.offsetHeight : 0;
    }

    // Seleccionar todos los enlaces de la barra de navegación
    const navLinks = document.querySelectorAll('.navbar-nav .nav-item .nav-link');

    // Collapse responsive navbar when toggler is visible
    const responsiveNavItems = [].slice.call(
        document.querySelectorAll('#navbarText .nav-link .nav-link')
    );

    // Función para manejar el scroll y actualizar el enlace activo
    function handleScroll() {
        let fromTop = window.scrollY + getOffsetTop();

        navLinks.forEach(link => {
            let section = document.querySelector(link.getAttribute('href'));

            if (
                section &&
                section.offsetTop <= fromTop &&
                section.offsetTop + section.offsetHeight > fromTop
            ) {
                link.classList.add('active');
            } else {
                link.classList.remove('active');
            }
        });
    }

    // Escuchar el evento de scroll para actualizar las clases activas
    window.addEventListener('scroll', handleScroll);

    // También llamar la función de scroll al cargar la página para manejar la posición inicial
    handleScroll();

    // Escuchar los clics en los enlaces de la barra de navegación
    responsiveNavItems.map(function (responsiveNavItem) {
        responsiveNavItem.addEventListener('click', function(event) {
            event.preventDefault(); // Prevenir el comportamiento predeterminado del enlace

            // Obtener el elemento de destino
            const targetId = responsiveNavItem.getAttribute('href');
            const targetElement = document.querySelector(targetId);

            if (targetElement) {
                // Calcular la posición de desplazamiento
                const targetPosition = targetElement.offsetTop - getOffsetTop();

                // Desplazarse suavemente hasta la posición deseada
                window.scrollTo({
                    top: targetPosition,
                    behavior: 'smooth'
                });

                // Actualizar las clases activas
                navLinks.forEach(link => link.classList.remove('active'));
                responsiveNavItem.classList.add('active');
            }
        });
    });

});
